/* Testimonials Section Styles */
.testimonialsSection {
  padding: 100px 0;
  background: #f8fafc;
  position: relative;
  overflow: hidden;
}

.testimonialsSection::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.02) 0%, rgba(118, 75, 162, 0.02) 100%);
  pointer-events: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  z-index: 1;
}

.sectionHeader {
  text-align: center;
  margin-bottom: 80px;
}

.badge {
  background: #667eea;
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  display: inline-block;
  margin-bottom: 20px;
}

.title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 20px;
}

.subtitle {
  font-size: 1.25rem;
  color: #718096;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.testimonialsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 40px;
  margin-bottom: 80px;
}

.testimonialCard {
  background: white;
  padding: 40px;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  border: 1px solid #f1f5f9;
  position: relative;
}

.testimonialCard::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.03), rgba(118, 75, 162, 0.03));
  border-radius: 16px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.testimonialCard:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
}

.testimonialCard:hover::before {
  opacity: 1;
}

.quoteIcon {
  width: 48px;
  height: 48px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 24px;
  position: relative;
  z-index: 1;
}

.quoteIcon i {
  font-size: 20px;
  color: white;
}

.testimonialText {
  font-size: 1.125rem;
  line-height: 1.6;
  color: #4a5568;
  margin-bottom: 24px;
  font-style: italic;
  position: relative;
  z-index: 1;
}

.testimonialAuthor {
  display: flex;
  align-items: center;
  gap: 16px;
  position: relative;
  z-index: 1;
}

.authorAvatar {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea, #764ba2);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 1.25rem;
  flex-shrink: 0;
}

.authorInfo {
  flex: 1;
}

.authorName {
  font-size: 1.125rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 4px;
}

.authorTitle {
  font-size: 0.875rem;
  color: #718096;
  margin-bottom: 4px;
}

.authorUniversity {
  font-size: 0.875rem;
  color: #667eea;
  font-weight: 600;
}

.rating {
  display: flex;
  gap: 4px;
  margin-top: 8px;
}

.star {
  color: #ffd700;
  font-size: 16px;
}

/* University Logos */
.universitiesSection {
  text-align: center;
  padding: 60px 0;
  background: white;
  border-radius: 20px;
  margin-top: 40px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
}

.universitiesTitle {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 40px;
}

.universitiesGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 40px;
  align-items: center;
}

.universityItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  padding: 20px;
  transition: all 0.3s ease;
  border-radius: 12px;
}

.universityItem:hover {
  transform: translateY(-4px);
  background: #f8fafc;
}

.universityLogo {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea, #764ba2);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 24px;
}

.universityName {
  font-size: 0.9rem;
  font-weight: 600;
  color: #4a5568;
  text-align: center;
  line-height: 1.4;
}

/* Statistics */
.statsSection {
  margin-top: 80px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  padding: 60px;
  border-radius: 20px;
  color: white;
  text-align: center;
}

.statsTitle {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 16px;
}

.statsSubtitle {
  font-size: 1.125rem;
  opacity: 0.9;
  margin-bottom: 40px;
}

.statsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 40px;
}

.statItem {
  color: white;
}

.statNumber {
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffd700;
  display: block;
  margin-bottom: 8px;
}

.statLabel {
  font-size: 1rem;
  opacity: 0.9;
  font-weight: 500;
}

/* Case Study */
.caseStudy {
  margin-top: 80px;
  background: white;
  padding: 60px;
  border-radius: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
}

.caseStudyHeader {
  text-align: center;
  margin-bottom: 40px;
}

.caseStudyTitle {
  font-size: 1.75rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 16px;
}

.caseStudySubtitle {
  font-size: 1.125rem;
  color: #718096;
}

.caseStudyContent {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  align-items: center;
}

.caseStudyText {
  color: #4a5568;
  line-height: 1.6;
}

.caseStudyHighlight {
  background: #f0f4ff;
  padding: 24px;
  border-radius: 12px;
  border-left: 4px solid #667eea;
  margin: 24px 0;
}

.caseStudyMetrics {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
}

.metricCard {
  background: #f8fafc;
  padding: 24px;
  border-radius: 12px;
  text-align: center;
}

.metricValue {
  font-size: 2rem;
  font-weight: 700;
  color: #667eea;
  display: block;
  margin-bottom: 8px;
}

.metricLabel {
  font-size: 0.875rem;
  color: #718096;
  font-weight: 500;
}

/* Responsive Design */
@media (max-width: 768px) {
  .testimonialsSection {
    padding: 60px 0;
  }
  
  .title {
    font-size: 2rem;
  }
  
  .testimonialsGrid {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .testimonialCard {
    padding: 30px;
  }
  
  .universitiesGrid {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
  
  .statsGrid {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
  
  .caseStudyContent {
    grid-template-columns: 1fr;
    gap: 40px;
  }
  
  .caseStudyMetrics {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  
  .statsSection {
    padding: 40px 20px;
  }
  
  .caseStudy {
    padding: 40px 20px;
  }
}

@media (max-width: 480px) {
  .universitiesGrid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .statsGrid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .statNumber {
    font-size: 2rem;
  }
} 