/* Benefits Section Styles */
.benefitsSection {
  padding: 100px 0;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  position: relative;
  overflow: hidden;
}

.benefitsSection::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23ffffff' fill-opacity='0.05'%3E%3Cpath d='M50 0l50 50-50 50L0 50z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  pointer-events: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  z-index: 1;
}

.sectionHeader {
  text-align: center;
  margin-bottom: 80px;
}

.badge {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  display: inline-block;
  margin-bottom: 20px;
}

.title {
  font-size: 2.5rem;
  font-weight: 700;
  color: white;
  margin-bottom: 20px;
}

.subtitle {
  font-size: 1.25rem;
  color: rgba(255, 255, 255, 0.9);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.benefitsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 40px;
}

.benefitCard {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 40px;
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  position: relative;
}

.benefitCard::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.benefitCard:hover {
  transform: translateY(-8px);
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.3);
}

.benefitCard:hover::before {
  opacity: 1;
}

.benefitIcon {
  width: 64px;
  height: 64px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 24px;
  position: relative;
  z-index: 1;
}

.benefitIcon i {
  font-size: 28px;
  color: #ffd700;
}

.benefitTitle {
  font-size: 1.375rem;
  font-weight: 700;
  color: white;
  margin-bottom: 16px;
  position: relative;
  z-index: 1;
}

.benefitDescription {
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.6;
  margin-bottom: 24px;
  position: relative;
  z-index: 1;
}

.benefitPoints {
  list-style: none;
  padding: 0;
  margin: 0;
  position: relative;
  z-index: 1;
}

.benefitPoints li {
  padding: 8px 0;
  color: rgba(255, 255, 255, 0.9);
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 0.95rem;
}

.benefitPoints li::before {
  content: '✓';
  color: #ffd700;
  font-weight: 700;
  font-size: 16px;
  background: rgba(255, 215, 0, 0.2);
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

/* ROI Section */
.roiContainer {
  margin-top: 80px;
  text-align: center;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 60px;
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.roiTitle {
  font-size: 2rem;
  font-weight: 700;
  color: white;
  margin-bottom: 16px;
}

.roiSubtitle {
  font-size: 1.125rem;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 40px;
}

.roiStats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 40px;
  margin-bottom: 40px;
}

.roiStat {
  color: white;
}

.roiNumber {
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffd700;
  display: block;
  margin-bottom: 8px;
}

.roiLabel {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 500;
}

.roiAction {
  background: white;
  color: #667eea;
  border: none;
  padding: 16px 32px;
  border-radius: 8px;
  font-weight: 700;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.roiAction:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(255, 255, 255, 0.3);
}

/* Comparison Table */
.comparisonTable {
  margin-top: 60px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  overflow: hidden;
}

.tableHeader {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  gap: 20px;
  font-weight: 700;
  color: white;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.tableRow {
  padding: 16px 20px;
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  gap: 20px;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.9);
}

.tableRow:last-child {
  border-bottom: none;
}

.checkmark {
  color: #ffd700;
  font-weight: 700;
  text-align: center;
}

.crossmark {
  color: #ff6b6b;
  font-weight: 700;
  text-align: center;
}

/* Responsive Design */
@media (max-width: 768px) {
  .benefitsSection {
    padding: 60px 0;
  }
  
  .title {
    font-size: 2rem;
  }
  
  .benefitsGrid {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .benefitCard {
    padding: 30px;
  }
  
  .roiContainer {
    padding: 40px 20px;
  }
  
  .roiStats {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
  
  .roiNumber {
    font-size: 2rem;
  }
  
  .tableHeader,
  .tableRow {
    grid-template-columns: 1fr;
    gap: 10px;
    text-align: center;
  }
  
  .tableHeader {
    display: none;
  }
  
  .tableRow {
    padding: 20px;
    background: rgba(255, 255, 255, 0.05);
    margin-bottom: 10px;
    border-radius: 8px;
    border: none;
  }
}

@media (max-width: 480px) {
  .roiStats {
    grid-template-columns: 1fr;
    gap: 20px;
  }
} 