.container {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

/* Table Container */
.table-container {
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  flex: 1;
  overflow: hidden;
  position: relative;
}

.table-wrapper {
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  position: relative;
  height: calc(100vh - 350px);
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px;
}

/* Table header styles */
.table-header {
  background-color: #d9eafd;
  position: sticky;
  top: 0;
  width: 100%;
}

.table-body-container {
  flex: 1;
  overflow-y: auto;
  overflow-x: auto;
  width: 100%;
}

.partners-table {
  width: 100%;
  table-layout: fixed;
  border-collapse: separate;
  border-spacing: 0;
  min-width: 1200px;
}

/* Common cell configuration */
.partners-table th,
.partners-table td {
  padding: 12px 8px;
  text-align: left;
  border-bottom: 1px solid #ddd;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.partners-table th {
  background-color: white;
  padding: 12px 8px;
  font-weight: 700;
  color: #2c5282;
  text-transform: uppercase;
  font-size: 13px;
  border-bottom: 2px solid #bee3f8;
}

/* Column widths */
.col-id {
  width: 8% !important;
}

.col-email {
  width: 25% !important;
}

.col-name {
  width: 18% !important;
}

.col-extra {
  width: 25% !important;
}

.col-date {
  width: 14% !important;
}

.col-actions {
  width: 10% !important;
}

.extraInfoContainer {
  max-width: 200px;
}

.extraInfoItem {
  background: #f1f3f4;
  padding: 4px 8px;
  margin: 2px 0;
  border-radius: 4px;
  font-size: 12px;
  word-break: break-word;
}

.extraInfoItem strong {
  color: #007bff;
}

.actions {
  display: flex;
  gap: 4px;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.emptyState {
  text-align: center;
  padding: 40px;
  color: #9ca3af;
  font-size: 16px;
}

.loadingContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 200px;
}

.loadingContainer p {
  margin-top: 10px;
  color: #4a90e2;
  font-size: 16px;
  font-weight: 500;
}

/* Tooltip */
.partners-table td[title]:hover::after {
  content: attr(title);
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  white-space: normal;
  max-width: 300px;
  z-index: 1000;
  margin-top: -30px;
  margin-left: 10px;
}

/* Custom scrollbar */
.table-body-container::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.table-body-container::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.table-body-container::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}

.table-body-container::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Responsive design */
@media (max-width: 1200px) {
  .table-body-container {
    overflow-x: auto;
  }
  
  .partners-table {
    min-width: 1200px;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 0 20px;
}

.header h2 {
  color: #333;
  margin: 0;
}

.addButton {
  background: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: background-color 0.3s ease;
}

.addButton:hover {
  background: #0056b3;
}

.searchContainer {
  margin-bottom: 20px;
  padding: 0 20px;
}

.searchBox {
  position: relative;
  max-width: 400px;
}

.searchIcon {
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
  color: #666;
}

.searchInput {
  width: 100%;
  padding: 12px 12px 12px 40px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 14px;
  transition: border-color 0.3s ease;
}

.searchInput:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

@media (max-width: 768px) {
  .container {
    padding: 10px;
  }

  .header {
    flex-direction: column;
    gap: 15px;
    align-items: stretch;
  }

  .addButton {
    justify-content: center;
  }

  .searchContainer {
    padding: 0 10px;
  }

  .tableContainer {
    margin: 0 10px;
    overflow-x: auto;
  }

  .table {
    min-width: 800px;
  }

  .table th,
  .table td {
    padding: 10px 8px;
    font-size: 14px;
  }

  .extraInfoCell {
    max-width: 150px;
  }

  .actionButtons {
    flex-direction: column;
    gap: 4px;
  }
}

@media (max-width: 480px) {
  .header h2 {
    font-size: 20px;
  }

  .addButton {
    padding: 10px 16px;
    font-size: 14px;
  }

  .table th,
  .table td {
    padding: 8px 6px;
    font-size: 12px;
  }

  .extraInfoItem {
    font-size: 11px;
    padding: 2px 6px;
  }
} 