/* Hero Section Styles */
.heroSection {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  position: relative;
  color: white;
  padding: 0; /* Remove fixed padding, let content determine height */
}

.heroSection::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grain" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="50" cy="50" r="0.5" fill="rgba(255,255,255,0.1)"/></pattern></defs><rect width="100" height="100" fill="url(%23grain)"/></svg>');
  opacity: 0.3;
  pointer-events: none;
}

/* Navigation */
.heroNav {
  position: relative; /* Changed from absolute to relative */
  z-index: 1000;
  padding: 20px 0;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.navContainer {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navLogo {
  display: flex;
  align-items: center;
  gap: 12px;
}

.logoIcon {
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 16px;
  color: white;
}

.logoText {
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
}

.btnLogin {
  background: rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: white;
  padding: 12px 20px;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 8px;
}

.btnLogin:hover {
  background: rgba(255, 255, 255, 0.25);
  transform: translateY(-2px);
}

/* Hero Content */
.heroContainer {
  max-width: 1200px;
  margin: 0 auto;
  padding: 80px 20px; /* Reduced padding */
  position: relative;
  z-index: 1;
}

.heroContent {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  align-items: center;
}

.heroText {
  max-width: 600px;
}

.heroBadge {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  display: inline-block;
  margin-bottom: 24px;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.heroTitle {
  font-size: 3.5rem;
  font-weight: 700;
  line-height: 1.1;
  margin-bottom: 24px;
}

/* Highlight Style - Đơn giản chỉ có màu chữ */
.highlight {
  background: linear-gradient(135deg, #ffd700, #ffed4e);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-size: inherit;
  font-weight: 800;
  display: inline-block;
}

/* Fallback cho browsers không hỗ trợ background-clip */
@supports not (-webkit-background-clip: text) {
  .highlight {
    color: #ffd700;
    background: none;
  }
}

.heroDescription {
  font-size: 1.25rem;
  line-height: 1.6;
  margin-bottom: 32px;
  opacity: 0.9;
}

.heroTargets {
  display: flex;
  gap: 24px;
  margin-bottom: 32px;
}

.targetItem {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 12px;
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.targetIcon {
  font-size: 2rem;
}

.targetItem h4 {
  font-size: 1rem;
  font-weight: 700;
  margin-bottom: 4px;
  color: white;
}

.targetItem p {
  font-size: 0.9rem;
  opacity: 0.8;
  margin: 0;
}

.heroStats {
  display: flex;
  gap: 40px;
  margin-bottom: 40px;
}

.statItem {
  text-align: center;
}

.statNumber {
  display: block;
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffd700;
}

.statLabel {
  font-size: 0.9rem;
  opacity: 0.8;
}

.heroButtons {
  display: flex;
  gap: 16px;
  margin-bottom: 32px;
}

.btnPrimary {
  background: #ffd700;
  color: #1a202c;
  border: none;
  padding: 16px 32px;
  border-radius: 8px;
  font-weight: 700;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 8px;
}

.btnPrimary:hover {
  background: #ffed4e;
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(255, 215, 0, 0.3);
}

.btnSecondary {
  background: transparent;
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 14px 32px;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btnSecondary:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.5);
  transform: translateY(-2px);
}

.heroFeatures {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
  margin-top: 32px;
}

.heroFeatures span {
  display: flex;
  align-items: center;
  gap: 8px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 12px 16px;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  font-size: 0.95rem;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.95);
  transition: all 0.3s ease;
  cursor: default;
}

.heroFeatures span:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(255, 255, 255, 0.1);
}

.heroFeatures span::before {
  content: '✓';
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  margin-right: 4px;
}

/* Responsive cho heroFeatures */
@media (max-width: 768px) {
  .heroFeatures {
    grid-template-columns: 1fr;
    gap: 12px;
  }
  
  .heroFeatures span {
    padding: 10px 14px;
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .heroFeatures {
    gap: 10px;
  }
  
  .heroFeatures span {
    padding: 8px 12px;
    font-size: 0.85rem;
  }
}

/* Image Slider Styles - Fixed for 2 slides */
.heroVisual {
  display: flex;
  justify-content: center;
  align-items: center;
}

.imageSlider {
  width: 100%;
  max-width: 500px;
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 
    0 25px 60px rgba(0, 0, 0, 0.4),
    0 0 0 1px rgba(255, 255, 255, 0.1),
    inset 0 0 0 1px rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.1);
}

.sliderContainer {
  position: relative;
  width: 100%;
  height: 420px;
  overflow: hidden;
  border-radius: 18px;
}

.slidesWrapper {
  display: flex;
  width: 200%; /* 2 slides x 100% */
  height: 100%;
  transition: transform 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.slide {
  width: 50%; /* 100% / 2 slides */
  height: 100%;
  position: relative;
  flex-shrink: 0; /* Ngăn slide bị co lại */
}

.slideImageContainer {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  border-radius: 16px;
}

.slideImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 0.8s ease;
  filter: brightness(0.9);
  display: block; /* Đảm bảo ảnh hiển thị */
}

.slideOverlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(
    transparent 0%, 
    rgba(0, 0, 0, 0.3) 40%, 
    rgba(0, 0, 0, 0.8) 100%
  );
  padding: 50px 28px 28px;
  color: white;
  z-index: 5; /* Thêm z-index để text nằm trên dots */
}

.slideTitle {
  font-size: 1.6rem;
  font-weight: 800;
  margin-bottom: 10px;
  color: white;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.6);
  letter-spacing: -0.02em;
  z-index: 6; /* Cao hơn dots */
  position: relative;
}

.slideDescription {
  font-size: 1rem;
  line-height: 1.6;
  opacity: 0.95;
  margin: 0;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.5);
  font-weight: 500;
  z-index: 6; /* Cao hơn dots */
  position: relative;
}

/* Navigation Arrows */
.sliderArrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255, 255, 255, 0.95);
  border: none;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  z-index: 15; /* Cao hơn tất cả */
  color: #333;
  font-size: 18px;
  box-shadow: 
    0 8px 25px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.sliderArrow:hover {
  background: white;
  transform: translateY(-50%) scale(1.15);
  box-shadow: 
    0 12px 35px rgba(0, 0, 0, 0.4),
    0 0 0 1px rgba(255, 255, 255, 0.2);
}

.sliderArrow:active {
  transform: translateY(-50%) scale(1.05);
}

.prevArrow {
  left: 20px;
}

.nextArrow {
  right: 20px;
}

/* Dots Navigation - Z-index thấp hơn text */
.sliderDots {
  position: absolute;
  bottom: 24px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 12px;
  z-index: 3; /* Thấp hơn slideOverlay (z-index: 5) */
}

.dot {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.6);
  background: transparent;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  backdrop-filter: blur(5px);
}

.dot:hover {
  border-color: rgba(255, 255, 255, 0.9);
  background: rgba(255, 255, 255, 0.4);
  transform: scale(1.2);
}

.activeDot {
  background: white;
  border-color: white;
  transform: scale(1.3);
  box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.3);
}

/* Hover Effects */
.imageSlider:hover .slideImage {
  transform: scale(1.08);
  filter: brightness(1);
}

.imageSlider:hover .sliderArrow {
  opacity: 1;
}

/* Enhanced styling for better visual hierarchy */
.imageSlider::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(135deg, 
    rgba(255, 255, 255, 0.2) 0%, 
    rgba(255, 255, 255, 0.05) 50%, 
    rgba(255, 255, 255, 0.1) 100%);
  border-radius: 22px;
  z-index: -1;
}

/* Debug - Fallback cho khi ảnh không load */
.slide {
  background: linear-gradient(135deg, #667eea, #764ba2);
}

.slideImage:not([src]),
.slideImage[src=""] {
  display: none;
}

/* Responsive Design Updates */
@media (max-width: 768px) {
  .imageSlider {
    max-width: 400px;
    border-radius: 16px;
  }
  
  .sliderContainer {
    height: 360px;
    border-radius: 14px;
  }
  
  .sliderArrow {
    width: 40px;
    height: 40px;
    font-size: 16px;
    z-index: 15;
  }
  
  .prevArrow {
    left: 16px;
  }
  
  .nextArrow {
    right: 16px;
  }
  
  .slideOverlay {
    padding: 40px 24px 24px;
    z-index: 5;
  }
  
  .slideTitle {
    font-size: 1.4rem;
  }
  
  .slideDescription {
    font-size: 0.95rem;
  }
  
  .dot {
    width: 12px;
    height: 12px;
  }
  
  .sliderDots {
    z-index: 3;
  }
}

@media (max-width: 480px) {
  .imageSlider {
    max-width: 320px;
    border-radius: 12px;
  }
  
  .sliderContainer {
    height: 280px;
    border-radius: 10px;
  }
  
  .sliderArrow {
    width: 36px;
    height: 36px;
    font-size: 14px;
  }
  
  .prevArrow {
    left: 12px;
  }
  
  .nextArrow {
    right: 12px;
  }
  
  .slideOverlay {
    padding: 30px 20px 20px;
  }
  
  .slideTitle {
    font-size: 1.2rem;
  }
  
  .slideDescription {
    font-size: 0.9rem;
  }
  
  .dot {
    width: 10px;
    height: 10px;
  }
  
  .sliderDots {
    gap: 10px;
  }
}

/* Animation for slide transitions */
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.slide {
  animation: slideIn 0.8s ease-out;
}

/* Responsive Design */
@media (max-width: 768px) {
  .heroContainer {
    padding: 60px 20px;
  }
  
  .heroContent {
    grid-template-columns: 1fr;
    gap: 50px;
  }
  
  .heroTitle {
    font-size: 2.5rem;
  }
  
  .heroDescription {
    font-size: 1.125rem;
  }
  
  .heroStats {
    gap: 20px;
  }
  
  .statNumber {
    font-size: 2rem;
  }
  
  .heroButtons {
    flex-direction: column;
    gap: 12px;
  }
  
  .heroTargets {
    flex-direction: column;
    gap: 16px;
  }
  
  .imageSlider {
    max-width: 350px;
  }
}

@media (max-width: 480px) {
  .heroTitle {
    font-size: 2rem;
  }
  
  .heroStats {
    flex-direction: column;
    gap: 16px;
  }
  
  .navContainer {
    padding: 0 15px;
  }
  
  .logoText {
    font-size: 1.25rem;
  }
} 