/* Features Section Styles */
.featuresSection {
  padding: 100px 0;
  background: white;
  position: relative;
  overflow: hidden;
}

.featuresSection::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.03) 0%, rgba(118, 75, 162, 0.03) 100%);
  pointer-events: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
}

/* Section Header */
.sectionHeader {
  text-align: center;
  margin-bottom: 80px;
}

.sectionBadge {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  display: inline-block;
  margin-bottom: 20px;
}

.sectionTitle {
  font-size: 2.5rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 20px;
  line-height: 1.2;
}

/* Highlight Style - Đơn giản chỉ có màu chữ */
.highlight {
  color: #667eea;
  font-size: inherit;
  font-weight: 800;
}

.sectionDescription {
  font-size: 1.25rem;
  color: #718096;
  max-width: 900px;
  margin: 0 auto;
  line-height: 1.6;
}

/* Features Grid */
.featuresGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 40px;
  margin-bottom: 100px;
}

.featureCard {
  background: white;
  padding: 40px;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  border: 1px solid #f1f5f9;
  position: relative;
  text-align: center;
}

.featureCard::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.05), rgba(118, 75, 162, 0.05));
  border-radius: 16px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.featureCard:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
}

.featureCard:hover::before {
  opacity: 1;
}

.featureIcon {
  width: 64px;
  height: 64px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 24px;
  color: white;
  font-size: 28px;
  position: relative;
  z-index: 1;
}

.featureTitle {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 16px;
  position: relative;
  z-index: 1;
}

.featureDescription {
  color: #718096;
  line-height: 1.6;
  position: relative;
  z-index: 1;
}

/* Color variants */
.featureCard.blue .featureIcon {
  background: linear-gradient(135deg, #4299e1, #3182ce);
}

.featureCard.green .featureIcon {
  background: linear-gradient(135deg, #48bb78, #38a169);
}

.featureCard.purple .featureIcon {
  background: linear-gradient(135deg, #9f7aea, #805ad5);
}

.featureCard.orange .featureIcon {
  background: linear-gradient(135deg, #ed8936, #dd6b20);
}

.featureCard.red .featureIcon {
  background: linear-gradient(135deg, #f56565, #e53e3e);
}

.featureCard.teal .featureIcon {
  background: linear-gradient(135deg, #4fd1c7, #38b2ac);
}

/* Features Showcase */
.featuresShowcase {
  background: linear-gradient(135deg, #f8fafc, #edf2f7);
  padding: 80px 60px;
  border-radius: 24px;
  margin-top: 80px;
}

.showcaseContent {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  align-items: center;
}

.showcaseText {
  /* Text content styles */
}

.showcaseTitle {
  font-size: 2rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 20px;
  line-height: 1.2;
}

.showcaseDescription {
  font-size: 1.125rem;
  color: #4a5568;
  line-height: 1.6;
  margin-bottom: 40px;
}

.showcaseStats {
  display: flex;
  gap: 40px;
  margin-bottom: 40px;
}

.showcaseStat {
  text-align: center;
}

.statNumber {
  display: block;
  font-size: 2.5rem;
  font-weight: 700;
  color: #667eea;
  margin-bottom: 8px;
}

.statLabel {
  font-size: 0.9rem;
  color: #718096;
  font-weight: 500;
}

.btnPrimary {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  padding: 16px 32px;
  border-radius: 8px;
  font-weight: 700;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.btnPrimary:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(102, 126, 234, 0.3);
}

/* Testimonial Cards */
.showcaseVisual {
  display: flex;
  justify-content: center;
}

.testimonialCards {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.testimonialCard {
  background: white;
  padding: 24px;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid #e2e8f0;
  transition: all 0.3s ease;
}

.testimonialCard:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.12);
}

.testimonialHeader {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
}

.avatar {
  width: 48px;
  height: 48px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 1.125rem;
  flex-shrink: 0;
}

.authorInfo h4 {
  font-size: 1rem;
  font-weight: 700;
  color: #1a202c;
  margin: 0 0 4px 0;
}

.authorInfo p {
  font-size: 0.875rem;
  color: #718096;
  margin: 0;
}

.testimonialText {
  color: #4a5568;
  line-height: 1.5;
  font-style: italic;
  margin: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .featuresSection {
    padding: 60px 0;
  }
  
  .sectionTitle {
    font-size: 2rem;
  }
  
  .featuresGrid {
    grid-template-columns: 1fr;
    gap: 30px;
    margin-bottom: 60px;
  }
  
  .featureCard {
    padding: 30px;
  }
  
  .featuresShowcase {
    padding: 60px 30px;
    margin-top: 60px;
  }
  
  .showcaseContent {
    grid-template-columns: 1fr;
    gap: 50px;
  }
  
  .showcaseTitle {
    font-size: 1.75rem;
  }
  
  .showcaseStats {
    gap: 20px;
    justify-content: center;
  }
  
  .statNumber {
    font-size: 2rem;
  }
  
  .testimonialCards {
    gap: 20px;
  }
  
  .testimonialCard {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .featuresGrid {
    grid-template-columns: 1fr;
  }
  
  .showcaseStats {
    flex-direction: column;
    gap: 20px;
  }
  
  .statNumber {
    font-size: 1.75rem;
  }
  
  .featuresShowcase {
    padding: 40px 20px;
  }
  
  .showcaseTitle {
    font-size: 1.5rem;
  }
  
  .showcaseDescription {
    font-size: 1rem;
  }
} 