/* CTA Section Styles - Updated for consistency with HeroSection */
.ctaSection {
  padding: 120px 0;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  position: relative;
  overflow: hidden;
}

.ctaSection::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grain" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="50" cy="50" r="0.5" fill="rgba(255,255,255,0.1)"/></pattern></defs><rect width="100" height="100" fill="url(%23grain)"/></svg>');
  opacity: 0.3;
  pointer-events: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  z-index: 1;
}

/* Main CTA - Updated styling */
.mainCta {
  text-align: center;
  margin-bottom: 120px;
}

.ctaContent {
  max-width: 800px;
  margin: 0 auto;
}

.ctaBadge {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  display: inline-block;
  margin-bottom: 24px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);
}

.ctaTitle {
  font-size: 3.5rem;
  font-weight: 700;
  color: white;
  margin-bottom: 24px;
  line-height: 1.1;
  letter-spacing: -0.02em;
}

.highlight {
  background: linear-gradient(135deg, #ffd700, #ffed4e);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-size: inherit;
  font-weight: 800;
  display: inline-block;
}

/* Fallback cho browsers không hỗ trợ background-clip */
@supports not (-webkit-background-clip: text) {
  .highlight {
    color: #ffd700;
    background: none;
  }
}

.ctaDescription {
  font-size: 1.25rem;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 40px;
  line-height: 1.6;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.ctaButtons {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-bottom: 50px;
}

.btnPrimaryLarge {
  background: #ffd700;
  color: #1a202c;
  border: none;
  padding: 18px 36px;
  border-radius: 12px;
  font-weight: 700;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 10px;
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.3);
}

.btnPrimaryLarge:hover {
  background: #ffed4e;
  transform: translateY(-3px);
  box-shadow: 0 12px 35px rgba(255, 215, 0, 0.4);
}

.btnSecondaryLarge {
  background: transparent;
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 16px 36px;
  border-radius: 12px;
  font-weight: 600;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 10px;
  backdrop-filter: blur(10px);
}

.btnSecondaryLarge:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.5);
  transform: translateY(-3px);
}

.ctaFeatures {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
  max-width: 600px;
  margin: 0 auto;
}

.featureItem {
  display: flex;
  align-items: center;
  gap: 12px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 16px 20px;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  font-weight: 500;
  color: rgba(255, 255, 255, 0.95);
  transition: all 0.3s ease;
}

.featureItem:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 255, 255, 0.1);
}

.featureIcon {
  color: #ffd700;
  font-size: 14px;
  flex-shrink: 0;
}

/* Contact Section - Glass morphism style */
.contactSection {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  padding: 80px;
  border-radius: 24px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 
    0 25px 60px rgba(0, 0, 0, 0.3),
    inset 0 0 0 1px rgba(255, 255, 255, 0.1);
}

.contactGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  align-items: start;
}

/* Contact Info Card */
.contactInfoCard {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  padding: 40px;
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  height: 100%;
}

.contactInfoCard h3 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 16px;
  color: white;
}

.contactInfoCard > p {
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 40px;
  line-height: 1.6;
  font-size: 1.125rem;
}

.contactItems {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.contactItem {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.contactItem:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}

.contactIcon {
  width: 48px;
  height: 48px;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  font-size: 18px;
  color: #ffd700;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.contactDetails {
  flex: 1;
}

.contactDetails h4 {
  font-size: 1.125rem;
  font-weight: 700;
  margin-bottom: 8px;
  color: white;
  line-height: 1.2;
}

.contactDetails p {
  color: rgba(255, 255, 255, 0.85);
  margin: 0 0 4px 0;
  font-size: 0.95rem;
  line-height: 1.4;
}

.contactDetails p:last-child {
  margin-bottom: 0;
}

/* Contact Form - Glass morphism style */
.contactFormWrapper {
  position: relative;
}

.contactForm {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  padding: 40px;
  border-radius: 20px;
  box-shadow: 
    0 25px 60px rgba(0, 0, 0, 0.2),
    inset 0 0 0 1px rgba(255, 255, 255, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.formHeader {
  text-align: center;
  margin-bottom: 32px;
}

.formHeader h3 {
  font-size: 1.75rem;
  font-weight: 700;
  margin-bottom: 8px;
  color: #1a202c;
}

.formHeader p {
  color: #718096;
  font-size: 0.95rem;
  margin: 0;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.formRow {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.formGroup {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.formGroup label {
  color: #4a5568;
  font-weight: 600;
  font-size: 0.95rem;
}

.formGroup input,
.formGroup select,
.formGroup textarea {
  padding: 14px 16px;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: white;
  color: #1a202c;
  font-family: inherit;
}

.formGroup input:focus,
.formGroup select:focus,
.formGroup textarea:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
  transform: translateY(-1px);
}

.formGroup textarea {
  resize: vertical;
  min-height: 100px;
  font-family: inherit;
}

.btnSubmit {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  padding: 16px 32px;
  border-radius: 12px;
  font-weight: 700;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-top: 8px;
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.3);
}

.btnSubmit:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(102, 126, 234, 0.4);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .contactGrid {
    grid-template-columns: 1fr;
    gap: 60px;
  }
  
  .contactSection {
    padding: 60px 40px;
  }
}

@media (max-width: 768px) {
  .ctaSection {
    padding: 80px 0;
  }
  
  .ctaTitle {
    font-size: 2.5rem;
  }
  
  .ctaButtons {
    flex-direction: column;
    align-items: center;
    gap: 16px;
  }
  
  .btnPrimaryLarge,
  .btnSecondaryLarge {
    width: 100%;
    max-width: 300px;
    justify-content: center;
  }
  
  .ctaFeatures {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  
  .contactSection {
    padding: 40px 30px;
  }
  
  .contactForm {
    padding: 30px;
  }
  
  .formRow {
    grid-template-columns: 1fr;
    gap: 0;
  }
  
  .contactInfoCard {
    padding: 30px;
  }
  
  .contactItem {
    align-items: center;
    padding: 16px;
  }
  
  .contactIcon {
    width: 44px;
    height: 44px;
    font-size: 16px;
  }
  
  .contactDetails h4 {
    font-size: 1rem;
  }
  
  .contactDetails p {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .ctaTitle {
    font-size: 2rem;
  }
  
  .ctaDescription {
    font-size: 1.125rem;
  }
  
  .contactSection {
    padding: 30px 20px;
  }
  
  .contactForm {
    padding: 24px;
  }
  
  .contactInfoCard {
    padding: 24px;
  }
  
  .contactInfoCard h3 {
    font-size: 1.75rem;
  }
  
  .contactItems {
    gap: 16px;
  }
  
  .contactItem {
    align-items: center;
    padding: 14px;
  }
  
  .contactIcon {
    width: 40px;
    height: 40px;
    font-size: 15px;
  }
  
  .contactDetails h4 {
    font-size: 0.95rem;
  }
  
  .contactDetails p {
    font-size: 0.85rem;
  }
} 