/* Pricing Section Styles */
.pricingSection {
  padding: 100px 0;
  background: white;
  position: relative;
  overflow: hidden;
}

.pricingSection::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.02) 0%, rgba(118, 75, 162, 0.02) 100%);
  pointer-events: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  z-index: 1;
}

.sectionHeader {
  text-align: center;
  margin-bottom: 80px;
}

.badge {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  display: inline-block;
  margin-bottom: 20px;
}

.title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 20px;
}

.subtitle {
  font-size: 1.25rem;
  color: #718096;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.pricingGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 40px;
  margin-bottom: 80px;
}

.pricingCard {
  background: white;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  border: 2px solid #f1f5f9;
  position: relative;
  overflow: hidden;
}

.pricingCard::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.03), rgba(118, 75, 162, 0.03));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.pricingCard:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
  border-color: #667eea;
}

.pricingCard:hover::before {
  opacity: 1;
}

/* Popular Plan */
.pricingCard.popular {
  border-color: #667eea;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  transform: scale(1.05);
}

.pricingCard.popular .planName,
.pricingCard.popular .planDescription,
.pricingCard.popular .featureItem {
  color: white;
}

.pricingCard.popular .planPrice {
  color: #ffd700;
}

.pricingCard.popular .planPeriod {
  color: rgba(255, 255, 255, 0.8);
}

.pricingCard.popular .actionButton {
  background: white;
  color: #667eea;
  border-color: white;
}

.pricingCard.popular .actionButton:hover {
  background: rgba(255, 255, 255, 0.9);
  transform: translateY(-2px);
}

.pricingCard.popular::before {
  display: none;
}

.popularBadge {
  position: absolute;
  top: -1px;
  left: 50%;
  transform: translateX(-50%);
  background: #ffd700;
  color: #1a202c;
  padding: 8px 24px;
  border-radius: 0 0 12px 12px;
  font-size: 0.875rem;
  font-weight: 700;
  z-index: 2;
}

.planHeader {
  text-align: center;
  margin-bottom: 32px;
  position: relative;
  z-index: 1;
}

.planName {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 8px;
}

.planDescription {
  color: #718096;
  font-size: 0.95rem;
  margin-bottom: 24px;
}

.planPricing {
  margin-bottom: 8px;
}

.planPrice {
  font-size: 3rem;
  font-weight: 700;
  color: #667eea;
}

.planPeriod {
  font-size: 1rem;
  color: #718096;
  font-weight: 500;
}

.planStudents {
  font-size: 0.875rem;
  color: #718096;
  font-weight: 500;
}

.featuresList {
  list-style: none;
  padding: 0;
  margin: 0 0 32px 0;
  position: relative;
  z-index: 1;
}

.featureItem {
  padding: 12px 0;
  color: #4a5568;
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 0.95rem;
}

.featureItem.included::before {
  content: '✓';
  color: #48bb78;
  font-weight: 700;
  font-size: 16px;
  background: rgba(72, 187, 120, 0.1);
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.featureItem.notIncluded {
  opacity: 0.5;
}

.featureItem.notIncluded::before {
  content: '✕';
  color: #f56565;
  font-weight: 700;
  font-size: 16px;
  background: rgba(245, 101, 101, 0.1);
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.actionButton {
  width: 100%;
  background: #667eea;
  border: 2px solid #667eea;
  color: white;
  padding: 16px 24px;
  border-radius: 12px;
  font-weight: 700;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  position: relative;
  z-index: 1;
}

.actionButton:hover {
  background: #5a6fd8;
  border-color: #5a6fd8;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.3);
}

.actionButton.secondary {
  background: transparent;
  border-color: #667eea;
  color: #667eea;
}

.actionButton.secondary:hover {
  background: #667eea;
  color: white;
}

/* Enterprise Section */
.enterpriseSection {
  background: linear-gradient(135deg, #f8fafc, #edf2f7);
  padding: 60px;
  border-radius: 20px;
  text-align: center;
  margin-top: 40px;
}

.enterpriseTitle {
  font-size: 2rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 16px;
}

.enterpriseDescription {
  font-size: 1.125rem;
  color: #718096;
  margin-bottom: 32px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.enterpriseFeatures {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 24px;
  margin-bottom: 40px;
}

.enterpriseFeature {
  display: flex;
  align-items: center;
  gap: 12px;
  justify-content: center;
  color: #4a5568;
  font-weight: 500;
}

.enterpriseFeature::before {
  content: '✓';
  color: #48bb78;
  font-weight: 700;
  font-size: 16px;
  background: rgba(72, 187, 120, 0.1);
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.enterpriseButton {
  background: #667eea;
  border: none;
  color: white;
  padding: 16px 32px;
  border-radius: 12px;
  font-weight: 700;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.enterpriseButton:hover {
  background: #5a6fd8;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.3);
}

/* FAQ Section */
.faqSection {
  margin-top: 80px;
}

.faqTitle {
  font-size: 2rem;
  font-weight: 700;
  color: #1a202c;
  text-align: center;
  margin-bottom: 40px;
}

.faqGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 24px;
}

.faqItem {
  background: #f8fafc;
  padding: 24px;
  border-radius: 12px;
  border-left: 4px solid #667eea;
}

.faqQuestion {
  font-size: 1.125rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 12px;
}

.faqAnswer {
  color: #718096;
  line-height: 1.6;
}

/* Responsive Design */
@media (max-width: 768px) {
  .pricingSection {
    padding: 60px 0;
  }
  
  .title {
    font-size: 2rem;
  }
  
  .pricingGrid {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .pricingCard {
    padding: 30px;
  }
  
  .pricingCard.popular {
    transform: none;
  }
  
  .planPrice {
    font-size: 2.5rem;
  }
  
  .enterpriseSection {
    padding: 40px 20px;
  }
  
  .enterpriseFeatures {
    grid-template-columns: 1fr;
    gap: 16px;
    text-align: left;
  }
  
  .faqGrid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .planPrice {
    font-size: 2rem;
  }
  
  .actionButton {
    font-size: 1rem;
    padding: 14px 20px;
  }
} 