.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal {
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #e8e8e8;
}

.titleSection {
  display: flex;
  align-items: center;
  gap: 12px;
}

.icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
}

.approveIcon {
  background: #d1fae5;
  color: #065f46;
}

.rejectIcon {
  background: #fee2e2;
  color: #dc2626;
}

.header h2 {
  margin: 0;
  color: #333;
  font-size: 1.25rem;
  font-weight: 600;
}

.closeButton {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #666;
  padding: 4px;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.closeButton:hover {
  background-color: #f5f5f5;
  color: #333;
}

.content {
  padding: 20px;
}

.requestInfo {
  background: #f8fafc;
  border-radius: 6px;
  padding: 16px;
  margin-bottom: 20px;
}

.requestInfo h3 {
  margin: 0 0 12px 0;
  color: #374151;
  font-size: 1rem;
  font-weight: 600;
}

.infoRow {
  display: flex;
  margin-bottom: 8px;
}

.label {
  font-weight: 500;
  color: #6b7280;
  min-width: 120px;
  margin-right: 12px;
}

.value {
  color: #1f2937;
  font-weight: 500;
}

.notesSection {
  margin-bottom: 20px;
}

.notesLabel {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #374151;
  font-size: 14px;
}

.required {
  color: #e53e3e;
}

.notesTextarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 14px;
  resize: vertical;
  min-height: 100px;
  transition: border-color 0.2s, box-shadow 0.2s;
  box-sizing: border-box;
  font-family: inherit;
}

.notesTextarea:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.warningMessage {
  background: #fef3c7;
  border-left: 4px solid #f59e0b;
  padding: 12px 16px;
  border-radius: 4px;
}

.warningMessage p {
  margin: 0;
  color: #92400e;
  font-size: 14px;
}

.actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  padding: 20px;
  border-top: 1px solid #e8e8e8;
  background: #f8fafc;
}

.cancelButton {
  padding: 10px 20px;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  color: #374151;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.2s;
}

.cancelButton:hover:not(:disabled) {
  background: #f3f4f6;
  border-color: #9ca3af;
}

.confirmButton {
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  color: white;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: background-color 0.2s;
}

.approveButton {
  background: #10b981;
}

.approveButton:hover:not(:disabled) {
  background: #059669;
}

.rejectButton {
  background: #ef4444;
}

.rejectButton:hover:not(:disabled) {
  background: #dc2626;
}

.confirmButton:disabled,
.cancelButton:disabled {
  opacity: 0.6;
  cursor: not-allowed;
} 