/* Footer Styles */
.footer {
  background: #1a202c;
  color: white;
  padding: 80px 0 40px;
  position: relative;
  overflow: hidden;
}

.footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.05) 0%, rgba(118, 75, 162, 0.05) 100%);
  pointer-events: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  z-index: 1;
}

.footerContent {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr 1fr;
  gap: 60px;
  margin-bottom: 60px;
}

.footerBrand {
  max-width: 350px;
}

.brandLogo {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 24px;
}

.logoIcon {
  width: 48px;
  height: 48px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 20px;
}

.brandName {
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
}

.brandDescription {
  color: #a0aec0;
  line-height: 1.6;
  margin-bottom: 24px;
}

.socialLinks {
  display: flex;
  gap: 16px;
}

.socialLink {
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #a0aec0;
  transition: all 0.3s ease;
  text-decoration: none;
}

.socialLink:hover {
  background: #667eea;
  color: white;
  transform: translateY(-2px);
}

.footerSection {
  /* Base styles for footer sections */
}

.sectionTitle {
  font-size: 1.125rem;
  font-weight: 700;
  color: white;
  margin-bottom: 24px;
}

.sectionLinks {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sectionLinks li {
  margin-bottom: 12px;
}

.sectionLinks a {
  color: #a0aec0;
  text-decoration: none;
  transition: color 0.3s ease;
  font-size: 0.95rem;
}

.sectionLinks a:hover {
  color: #667eea;
}

.contactInfo {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.contactItem {
  display: flex;
  align-items: center;
  gap: 12px;
  color: #a0aec0;
  font-size: 0.95rem;
}

.contactIcon {
  width: 20px;
  height: 20px;
  color: #667eea;
  flex-shrink: 0;
}

.newsletter {
  background: rgba(255, 255, 255, 0.05);
  padding: 24px;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 40px;
}

.newsletterContent {
  margin-bottom: 20px;
}

.newsletterTitle {
  font-size: 1.125rem;
  font-weight: 700;
  color: white;
  margin-bottom: 12px;
}

.newsletterDescription {
  color: #a0aec0;
  font-size: 0.9rem;
  line-height: 1.5;
}

.newsletterForm {
  display: flex;
  gap: 8px;
}

.newsletterInput {
  flex: 1;
  padding: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  font-size: 0.9rem;
  outline: none;
  transition: border-color 0.3s ease;
}

.newsletterInput::placeholder {
  color: #a0aec0;
}

.newsletterInput:focus {
  border-color: #667eea;
}

.newsletterButton {
  background: #667eea;
  border: none;
  color: white;
  padding: 12px 20px;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  gap: 8px;
}

.newsletterButton:hover {
  background: #5a6fd8;
  transform: translateY(-1px);
}

.footerBottom {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 40px;
}

.footerBottomContent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}

.copyright {
  color: #a0aec0;
  font-size: 0.9rem;
}

.footerBottomLinks {
  display: flex;
  gap: 32px;
}

.footerBottomLinks a {
  color: #a0aec0;
  text-decoration: none;
  font-size: 0.9rem;
  transition: color 0.3s ease;
}

.footerBottomLinks a:hover {
  color: #667eea;
}

/* Responsive Design */
@media (max-width: 768px) {
  .footer {
    padding: 60px 0 30px;
  }
  
  .footerContent {
    grid-template-columns: 1fr;
    gap: 40px;
  }
  
  .footerBrand {
    max-width: none;
  }
  
  .newsletterForm {
    flex-direction: column;
    gap: 12px;
  }
  
  .footerBottomContent {
    flex-direction: column;
    text-align: center;
    gap: 24px;
  }
  
  .footerBottomLinks {
    gap: 20px;
    flex-wrap: wrap;
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .socialLinks {
    justify-content: center;
  }
  
  .footerBottomLinks {
    flex-direction: column;
    gap: 12px;
    text-align: center;
  }
} 