.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

/* Remove duplicate font declarations */
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

/* Remove duplicate html and body font declarations */
html, body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden; /* Only hide horizontal scroll */
  /* Allow normal vertical scrolling */
}

/* Update login form elements to use clamp values */
.login-container input {
  margin: 0.625rem;
  padding: 0.625rem;
  width: 12.5rem;
  font-size: clamp(0.75rem, 1.2vw, 0.8125rem);
}

.login-container button {
  padding: 0.625rem 1.25rem;
  background-color: #2c3e50;
  color: white;
  border: none;
  cursor: pointer;
  font-size: clamp(0.75rem, 1.2vw, 0.8125rem);
}

.login-container button:hover {
  background-color: #34495e;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#root {
  /* Allow normal height and scrolling */
}

.App {
  /* Allow normal height and scrolling */
}

/* Login styles */
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.login-container input {
  margin: 10px;
  padding: 10px;
  width: 200px;
}

.login-container button {
  padding: 10px 20px;
  background-color: #2c3e50;
  color: white;
  border: none;
  cursor: pointer;
}

.login-container button:hover {
  background-color: #34495e;
}

/* App container for logged-in users */
.app-container {
  display: flex;
  min-height: 100vh; /* Changed from height: 100% */
  /* Remove overflow: hidden to allow normal scrolling */
}

.sidebar {
  flex: 0 0 250px; /* Fixed width instead of flex: 10 */
  background-color: #2c3e50;
  color: white;
  min-height: 100vh;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #ccc #2c3e50;
}

.sidebar::-webkit-scrollbar {
  width: 8px;
}

.sidebar::-webkit-scrollbar-track {
  background: #2c3e50;
}

.sidebar::-webkit-scrollbar-thumb { 
  background: #444;
  border-radius: 4px;
}

.sidebar::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.main-content {
  flex: 1; /* Take remaining space */
  background-color: #f2f4f7;
  padding: 20px;
  min-height: 100vh; /* Changed from height + overflow */
  /* Remove overflow-y: auto to allow normal page scrolling */
}

/* Custom scrollbar for webkit browsers */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}