
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal {
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  max-width: 500px;
  width: 90%;
  margin: 20px;
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.header {
  padding: 20px 20px 0 20px;
  border-bottom: 1px solid #e9ecef;
}

.header h3 {
  margin: 0;
  color: #dc3545;
  font-size: 18px;
  font-weight: 600;
}

.content {
  padding: 20px;
}

.content p {
  margin: 0 0 15px 0;
  color: #333;
  font-size: 16px;
}

.customerInfo {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 6px;
  padding: 15px;
  margin: 15px 0;
}

.infoRow {
  margin-bottom: 8px;
  font-size: 14px;
}

.infoRow:last-child {
  margin-bottom: 0;
}

.infoRow strong {
  color: #495057;
  display: inline-block;
  min-width: 60px;
}

.warning {
  background: #fff3cd;
  border: 1px solid #ffeaa7;
  border-radius: 4px;
  padding: 12px;
  color: #856404;
  font-size: 14px;
  margin-top: 15px;
}

.warning strong {
  color: #d63384;
}

.footer {
  padding: 0 20px 20px 20px;
  display: flex;
  gap: 10px;
  justify-content: flex-end;
}

.cancelButton,
.confirmButton {
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.3s ease;
}

.cancelButton {
  background: #6c757d;
  color: white;
}

.cancelButton:hover {
  background: #5a6268;
}

.confirmButton {
  background: #dc3545;
  color: white;
}

.confirmButton:hover {
  background: #c82333;
}

@media (max-width: 480px) {
  .modal {
    width: 95%;
    margin: 10px;
  }

  .header,
  .content,
  .footer {
    padding: 15px;
  }

  .footer {
    flex-direction: column;
  }

  .cancelButton,
  .confirmButton {
    width: 100%;
  }
}