.container {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

/* Table Container */
.table-container {
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  flex: 1;
  overflow: hidden;
  position: relative;
}

.table-wrapper {
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  position: relative;
  height: calc(100vh - 350px);
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px;
}

/* Table header styles */
.table-header {
  background-color: #d9eafd;
  position: sticky;
  top: 0;
  width: 100%;
}

.table-body-container {
  flex: 1;
  overflow-y: auto;
  overflow-x: auto;
  width: 100%;
}

.requests-table {
  width: 100%;
  table-layout: fixed;
  border-collapse: separate;
  border-spacing: 0;
  min-width: 1400px;
}

/* Common cell configuration */
.requests-table th,
.requests-table td {
  padding: 12px 8px;
  text-align: left;
  border-bottom: 1px solid #ddd;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.requests-table th {
  background-color: white;
  padding: 12px 8px;
  font-weight: 700;
  color: #2c5282;
  text-transform: uppercase;
  font-size: 13px;
  border-bottom: 2px solid #bee3f8;
}

/* Column widths */
.col-organization {
  width: 20% !important;
}

.col-owner {
  width: 15% !important;
}

.col-email {
  width: 18% !important;
}

.col-status {
  width: 10% !important;
}

.col-requested {
  width: 12% !important;
}

.col-reviewed {
  width: 12% !important;
}

.col-notes {
  width: 18% !important;
}

.col-actions {
  width: 15% !important;
}

.organizationName {
  font-weight: 600;
  color: #1f2937;
}

.emailLink {
  color: #3b82f6;
  text-decoration: none;
  transition: color 0.2s;
}

.emailLink:hover {
  color: #1d4ed8;
  text-decoration: underline;
}

.statusBadge {
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.statusPending {
  background: #fef3c7;
  color: #d97706;
}

.statusApproved {
  background: #d1fae5;
  color: #065f46;
}

.statusRejected {
  background: #fee2e2;
  color: #dc2626;
}

.notes {
  max-width: 180px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #6b7280;
  font-style: italic;
}

.actions {
  display: flex;
  gap: 4px;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.approveButton {
  background: #10b981;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 6px 8px;
  cursor: pointer;
  transition: background-color 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.approveButton:hover {
  background: #059669;
}

.rejectButton {
  background: #ef4444;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 6px 8px;
  cursor: pointer;
  transition: background-color 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.rejectButton:hover {
  background: #dc2626;
}

.emptyState {
  text-align: center;
  padding: 40px;
  color: #9ca3af;
  font-size: 16px;
}

.loadingContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 200px;
}

.loadingContainer p {
  margin-top: 10px;
  color: #4a90e2;
  font-size: 16px;
  font-weight: 500;
}

/* Tooltip */
.requests-table td[title]:hover::after {
  content: attr(title);
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  white-space: normal;
  max-width: 300px;
  z-index: 1000;
  margin-top: -30px;
  margin-left: 10px;
}

/* Custom scrollbar */
.table-body-container::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.table-body-container::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.table-body-container::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}

.table-body-container::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Responsive design */
@media (max-width: 1200px) {
  .table-body-container {
    overflow-x: auto;
  }
  
  .requests-table {
    min-width: 1400px;
  }
} 