/* Services Section Styles */
.servicesSection {
  padding: 100px 0;
  background: #f8fafc;
  position: relative;
  overflow: hidden;
}

.servicesSection::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.02) 0%, rgba(118, 75, 162, 0.02) 100%);
  pointer-events: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  z-index: 1;
}

/* Section Header Styles */
.sectionHeader {
  text-align: center;
  margin-bottom: 80px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.badgeContainer {
  margin-bottom: 24px;
}

.sectionBadge {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  padding: 10px 20px;
  border-radius: 25px;
  font-size: 14px;
  font-weight: 600;
  display: inline-block;
  border: 2px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.2);
  transition: all 0.3s ease;
}

.sectionBadge:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.3);
}

.sectionTitle {
  font-size: 2.75rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 24px;
  line-height: 1.2;
  letter-spacing: -0.025em;
}

/* Highlight Style - Đơn giản chỉ có màu chữ */
.highlight {
  color: #667eea;
  font-size: inherit;
  font-weight: 800;
}

.sectionDescription {
  font-size: 1.25rem;
  color: #4a5568;
  line-height: 1.6;
  margin-bottom: 40px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

/* Target Audience */
.targetAudience {
  display: flex;
  justify-content: center;
  gap: 24px;
  margin-top: 32px;
}

.audienceItem {
  display: flex;
  align-items: center;
  gap: 12px;
  background: white;
  padding: 16px 24px;
  border-radius: 50px;
  border: 2px solid #667eea;
  color: #667eea;
  font-weight: 600;
  font-size: 0.95rem;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.1);
  transition: all 0.3s ease;
  cursor: pointer;
}

.audienceItem:hover {
  background: #667eea;
  color: white;
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.25);
}

.audienceIcon {
  font-size: 18px;
  transition: transform 0.3s ease;
}

.audienceItem:hover .audienceIcon {
  transform: scale(1.1);
}

/* Services Grid */
.servicesGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  margin-bottom: 100px;
}

.serviceCard {
  background: white;
  padding: 40px;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  border: 1px solid #f1f5f9;
}

.serviceCard::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #667eea, #764ba2);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.serviceCard:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 45px rgba(0, 0, 0, 0.15);
}

.serviceCard:hover::before {
  opacity: 1;
}

.serviceIcon {
  width: 64px;
  height: 64px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 24px;
  color: white;
  font-size: 26px;
  transition: transform 0.3s ease;
}

.serviceCard:hover .serviceIcon {
  transform: scale(1.05);
}

.serviceTitle {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1a202c;
  margin-bottom: 16px;
}

.serviceDescription {
  color: #718096;
  line-height: 1.6;
  margin-bottom: 24px;
  flex-grow: 1;
}

.serviceFeatures {
  list-style: none;
  padding: 0;
  margin: 0 0 24px 0;
}

.serviceFeatures li {
  padding: 8px 0;
  color: #4a5568;
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 0.95rem;
}

.serviceFeatures li::before {
  content: '✓';
  color: #48bb78;
  font-weight: 700;
  font-size: 16px;
  width: 20px;
  height: 20px;
  background: rgba(72, 187, 120, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.useCases {
  margin: 20px 0;
  padding: 20px 0;
  border-top: 1px solid #e2e8f0;
  border-bottom: 1px solid #e2e8f0;
}

.useCase {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  font-size: 0.9rem;
  color: #4a5568;
  padding: 6px 12px;
  background: #f8fafc;
  border-radius: 20px;
  border: 1px solid #e2e8f0;
}

.useCase:last-child {
  margin-bottom: 0;
}

.useCaseIcon {
  color: #667eea;
  font-size: 14px;
  flex-shrink: 0;
}

.serviceFooter {
  margin-top: auto;
  padding-top: 24px;
}

.serviceAction {
  background: transparent;
  border: 2px solid #667eea;
  color: #667eea;
  padding: 14px 28px;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 8px;
  justify-content: center;
  width: 100%;
  font-size: 1rem;
}

.serviceAction:hover {
  background: #667eea;
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.3);
}

/* CTA Section */
.servicesCta {
  background: linear-gradient(135deg, #667eea, #764ba2);
  padding: 80px 60px;
  border-radius: 24px;
  text-align: center;
  color: white;
  position: relative;
  overflow: hidden;
}

.servicesCta::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="pattern" width="20" height="20" patternUnits="userSpaceOnUse"><circle cx="10" cy="10" r="1" fill="rgba(255,255,255,0.1)"/></pattern></defs><rect width="100" height="100" fill="url(%23pattern)"/></svg>');
  opacity: 0.5;
}

.ctaContent {
  position: relative;
  z-index: 1;
}

.ctaContent h3 {
  font-size: 2.25rem;
  font-weight: 700;
  margin-bottom: 20px;
  line-height: 1.2;
}

.ctaContent p {
  font-size: 1.25rem;
  opacity: 0.9;
  margin-bottom: 40px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}

.ctaButtons {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.btnPrimary {
  background: white;
  color: #667eea;
  border: none;
  padding: 18px 36px;
  border-radius: 12px;
  font-weight: 700;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  box-shadow: 0 4px 15px rgba(255, 255, 255, 0.2);
}

.btnPrimary:hover {
  background: #f8fafc;
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(255, 255, 255, 0.3);
}

.btnSecondary {
  background: transparent;
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.5);
  padding: 16px 36px;
  border-radius: 12px;
  font-weight: 700;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 10px;
}

.btnSecondary:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.8);
  transform: translateY(-3px);
}

/* Color variants */
.serviceCard.blue .serviceIcon {
  background: linear-gradient(135deg, #4299e1, #3182ce);
}

.serviceCard.green .serviceIcon {
  background: linear-gradient(135deg, #48bb78, #38a169);
}

.serviceCard.orange .serviceIcon {
  background: linear-gradient(135deg, #ed8936, #dd6b20);
}

.serviceCard.purple .serviceIcon {
  background: linear-gradient(135deg, #9f7aea, #805ad5);
}

.serviceCard.red .serviceIcon {
  background: linear-gradient(135deg, #f56565, #e53e3e);
}

.serviceCard.teal .serviceIcon {
  background: linear-gradient(135deg, #4fd1c7, #38b2ac);
}

/* Responsive Design */
@media (max-width: 1200px) {
  .servicesGrid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .servicesSection {
    padding: 60px 0;
  }
  
  .sectionTitle {
    font-size: 2.25rem;
  }
  
  .sectionDescription {
    font-size: 1.125rem;
  }
  
  .targetAudience {
    flex-direction: column;
    gap: 16px;
    align-items: center;
  }
  
  .servicesGrid {
    grid-template-columns: 1fr;
    gap: 30px;
    margin-bottom: 60px;
  }
  
  .serviceCard {
    padding: 30px;
  }
  
  .servicesCta {
    padding: 60px 30px;
  }
  
  .ctaContent h3 {
    font-size: 1.75rem;
  }
  
  .ctaContent p {
    font-size: 1.125rem;
  }
  
  .ctaButtons {
    flex-direction: column;
    gap: 16px;
    align-items: center;
  }
}

@media (max-width: 480px) {
  .sectionTitle {
    font-size: 2rem;
  }
  
  .servicesGrid {
    grid-template-columns: 1fr;
  }
  
  .servicesCta {
    padding: 40px 20px;
  }
  
  .ctaContent h3 {
    font-size: 1.5rem;
  }
} 